.header-wrapper {
  width: 80vw;
  margin-top: 8vh;
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    font-size: 1rem;
    text-align: center;
    text-transform: uppercase;
    @include media(">=phone", "<lgphone") {
      font-size: 14px;
    }
    @include media(">=lgphone", "<tablet") {
      font-size: 16px;
    }
    @include media(">=tablet", "<desktop") {
      font-size: 16px;
    }
  }
  .heading-wrapper {
    p {
      font-size: 4rem;
      text-align: center;
      line-height: 20px;
      @include media(">=phone", "<lgphone") {
        font-size: 1.5rem;
        line-height: 10px;
      }
      @include media(">=lgphone", "<tablet") {
        font-size: 1.5rem;
        line-height: 10px;
      }
      @include media(">=tablet", "<desktop") {
        font-size: 2.5rem;
          line-height: 10px;
      }
    }
  }
}
